
import Aplayer from 'vue-aplayer'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
Tabbar,
TabbarItem,
Group,
Cell,
XHeader,
Tab,
TabItem,
Swiper,
SwiperItem,
Flexbox,
FlexboxItem,
Aplayer
},
created(){
this.getData();
},
data () {
return {
id: this.$route.params.id,
coverImgUrl:'',
name:'',
nickname:'',
avatarUrl:'',
backgroundImg:'',
folderNum:0,
fileNum:0,
shareNum:0,
musicList:[],
musicSource:'',
musicPic:'',
author:'',
flag:false,
}
},
methods: {
getData() {
this.$api.get('/playlist/detail?id=' + this.id, null, r => {
this.coverImgUrl = r.result.coverImgUrl;
this.name = r.result.name;
this.nickname = r.result.creator.nickname;
this.avatarUrl = r.result.creator.avatarUrl;
this.backgroundImg = r.result.creator.backgroundUrl;
this.folderNum = r.result.subscribedCount;
this.fileNum = r.result.commentCount;
this.shareNum = r.result.shareCount;
this.musicList = r.result.tracks;
})
},
playMusic(id,name) {
this.$api.get('/music/url?id=' + id, null, r => {
this.musicSource = r.data[0].url;
this.author = name;
this.flag = true;
});
}
}
}



