
import draggable from 'vuedraggable'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default {
components: {
Tabbar,
TabbarItem,
Group,
Cell,
XHeader,
Tab,
TabItem,
Swiper,
SwiperItem,
draggable,
Grid,
GridItem,
Flexbox,
FlexboxItem,
ViewBox
},
data () {
return {
list: [],
list2:[
{id:"id1",it:{name:'点击拖动'}},
{id:"id2",it:{name:'点击拖动'}},
{id:"id3",it:{name:'点击拖动'}},
{id:"id4",it:{name:'点击拖动'}}
],
tuijian:[],
newList:[]
}
},
created(){
this.getData();
this.getTuijianData();
this.getNewData();
},
methods: {
getdata (evt) {
console.log(evt.draggedContext.element.id)
},
datadragEnd (evt) {
console.log('拖动前的索引 :' + evt.oldIndex);
console.log('拖动后的索引 :' + evt.newIndex);
},
getData(){
this.$api.get('/banner', null, r => {
this.list = r.banners;
})
},
getTuijianData(){
this.$api.get('/personalized?limit=6', null, r => {
this.tuijian = r.result;
})
},
getNewData(){
this.$api.get('/top/album?offset=0&limit=6', null, r => {
this.newList = r.albums;
})
},
selList(id){
this.$defines.setShow(true);
this.$router.push('/detail/'+id);
}
}
}



